<template>
  <div class="home">
    <HelloWorld msg="FAKE FLIX" />
    <div>
      <b-form @submit="onSubmit" id='login_form'>
        <b-form-group id="input-group-1">
          <b-form-input id="input-1" v-model="form.username" type="text" placeholder="Enter username"
          ></b-form-input>
          <b-form-input
            id="input-2"
            v-model="form.password"
            type="password"
            placeholder="Enter password"
          ></b-form-input>
        </b-form-group>
        <b-button type="submit" variant="primary">S'identifier</b-button>
      </b-form>
      <h3 id="error_login">{{error_login}}</h3>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import HelloWorld from "@/components/HelloWorld.vue";
import 'es6-promise/auto'
import store from './Home.js'

export default {
  store: store,
  name: "Home",
  components: {
    HelloWorld
  },
  data: function() {
    return {
      createdToken:
        "https://api.themoviedb.org/3/authentication/token/new?api_key=b94d0075dc634f6f48d56ae29e58f3ce",
      sessionLogin:
        "https://api.themoviedb.org/3/authentication/token/validate_with_login?api_key=b94d0075dc634f6f48d56ae29e58f3ce",
      sessionId: "https://api.themoviedb.org/3/authentication/session/new?api_key=b94d0075dc634f6f48d56ae29e58f3ce",
      request_token: "",
      session_idToken: "",
      error_login:"",
      data_success: Boolean,
      form: {
        username: "",
        password: ""
      }
    };
  },
  computed: {
    session_id: function() {
      return store.state.session_id
    },
    
    getsession_id(){
      return this.session_id
    }
  },  
  methods: {
    setSession_IdValue(){
      let value = this.session_idToken;
      this.$store.dispatch('setsession_idValue',value)
    },
    createRequestToken() {
      fetch(this.createdToken)
        .then(response => {
          return response.json().then(data => {
            this.request_token = data.request_token;
            this.createSessionLogin();
          });
        })
        .catch(function(error) {
          console.log("Une erreur s'est produite");
          console.log(error);
        });
    },

    createSessionLogin() {
      fetch(this.sessionLogin, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json"
        },
        method: "POST",
        body: JSON.stringify({
          username: this.form.username,
          password: this.form.password,
          request_token: this.request_token
        })
      })
        .then(response => {
          return response.json().then(data => {
            this.session_idToken = data.request_token; 
            this.createSessionId();
          });
        })
        .catch(function(error) {
          console.log("Une erreur s'est produite");
          console.log(error);
        });
    },

   createSessionId() {
      fetch(this.sessionId, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json"
        },
        method: "POST",
        body: JSON.stringify({
          request_token: this.session_idToken
        })
      })
        .then(response => {
          return response.json().then(data => {
            console.log("Données Jsonnizées");
            this.session_idToken = data.session_id;
            this.data_success = data.success;
            this.redirect();
            this.setSession_IdValue();
          });
        })
        .catch(function(error) {
          console.log("Une erreur s'est produite");
          console.log(error);
        });
    },

    redirect(){
      if (this.data_success == true){
        this.$router.push({ name: 'Accueil', query: { redirect: '/accueil' } });
      }else{
        this.error_login = "Votre login ou mot de passe est erroné"
      }
    },

    /*getUserDetails(){
      fetch()
        .then(response => {
          return response.json().then(data => {
            console.log("Données Jsonnizées");
            console.log(data);
            console.log("Session_ID ");
          });
        })
        .catch(function(error) {
          console.log("Une erreur s'est produite");
          console.log(error);
        });
    },*/

    onSubmit() {
      this.createRequestToken();
    }
  }
};
</script>

<style>
#login_form{
  width: 20%;
  margin-left: 40%;
}

#error_login{
  margin-top: 25px;
  color: red;
}
</style>