import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

let store = new Vuex.Store({
    state: {
        session_id: "",
    },
    mutations: {
        setsession_idValue(state, value) {
            state.session_id = value;
        }
    },
    actions: {
        setsession_idValue(context, value) {
            context.commit('setsession_idValue', value)
        },
    },
    getters: {},

})

global.store = store

export default store